import { setSelectionRange } from '@testing-library/user-event/dist/utils';
import React, { useState } from 'react';

import './styleSpidder.css';

import env from './variables_entorno'


const ChatMessage = ({ isConversation, setIsConversation, itemMensaje, lstUsers, currentUser, setAnotherPerson }) => {

    const { sender, receiver, message, time } = itemMensaje;


    const anotherPerson = sender == currentUser ? receiver : sender;//sacamos el visitante digamos, el usuario que no soy yo
    //si yo soy el que envia, la otra persona es el receiver, de lo contrario es sender (es para configurar la foto de perfil)


    //como se va a alinear el mensaje horizontalmente de acuerdo a "quien envía"
    const alignMessage = sender == currentUser ? "right" : "left";





    // console.log("%c " +  lstUsers.filter((item)=> item.username  == sender)[0].urlImage , "background:violet")



     


    return (
        <>

            <>
                <div style={{
                    width: "auto", background: 'linear-gradient(to right, #FF738A, pink, #FF738A)', margin: '0.5rem', padding: '0.3rem', paddingLeft: '0.5rem',
                    textAlign: 'left', cursor: 'pointer', borderRadius: '5px'
                }}
                    onClick={() => {
                        setIsConversation(!isConversation)
                        setAnotherPerson(anotherPerson)
                    }} //no tiene relacion directa, si no que se relaciona desde el main
                >

                    <section className="sp-ps-sectionProfile">
                        <img className="sp-ps-img" src={lstUsers.filter((item) => item.username == anotherPerson)[0].urlImage} />
                        <p style={{
                            background: '#FF738A',
                            display: 'inline-block',
                            marginTop: '0.3rem',
                            paddingLeft: '2rem',
                            paddingRight: '2rem',
                            borderRadius: '2rem'
                        }}>  {lstUsers.filter((item) => item.username == anotherPerson)[0].username}  </p>

                    </section>


                    <p style={{ marginTop: "0.5rem", fontSize: "0.9rem", textAlign: alignMessage }}>{message}</p>
                    {/* <p>{sender} HACIA {receiver}</p> */}

                    <p>{msToHours(time)}</p>

                </div>
            </>
        </>
    );
};




function msToHours(ms) {
    let fecha = new Date(Number(ms))

    let day = fecha.getDate();
    let month = fecha.getMonth() + 1;
    let year = fecha.getFullYear();
    let hour = fecha.getHours();
    let minute = fecha.getMinutes();

    return (hour.toString().padStart(2, '0')
        + ":"
        + minute.toString().padStart(2, '0')
        + "  "
        + day.toString().padStart(2, '0')
        + "-"
        + month.toString().padStart(2, '0')
        + "-"
        + year)


}











export default ChatMessage;

